<template>
  <div id="deleteRule" style="display: inline-block">
    <v-icon title="删除角色" class="ml-4" @click.stop="openDeleteRuleDialog">mdi-delete</v-icon>
    <v-dialog v-model="dialog" persistent max-width="300px">
      <v-card>
        <v-card-title>
          确定删除该角色吗?
          <v-spacer />
          <v-icon aria-label="Close" @click="closeDeleteRuleDialog">mdi-close</v-icon>
        </v-card-title>

        <v-card-text class="pb-6 pt-12 text-center">
          <v-btn class="mr-3" text @click="closeDeleteRuleDialog">取消</v-btn>
          <v-btn color="success" text @click="doDeleteRole">确定</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false
  }),

  props: {
    roleId: Number
  },

  methods: {
    // 打开的对话框
    openDeleteRuleDialog() {
      this.dialog = true;
    },

    // 关闭对话框
    closeDeleteRuleDialog() {
      this.dialog = false;
    },

    // 删除角色
    doDeleteRole() {
      this.$http
        .post("/user/api/v1/role/delete/" + this.roleId)
        .then(res => {
          if (res.success) {
            res.msg = "角色删除成功";
          }
          this.$store.commit("SET_SNACK", res);

          // 刷新角色列表
          this.$emit("getRoles");
        })
        .catch(error => {});

      this.closeDeleteRuleDialog();
    }
  }
};
</script>